<template>
  <div class="product_list">
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">home</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/scientific' }"
          >领域研究前言</el-breadcrumb-item
        >
        <el-breadcrumb-item>信息列表</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="product_main">
      <div class="title">领域期刊专题锦集</div>
      <div class="characteristic">
        <div class="characteristic_info" v-for="(item, i) of 20" :key="i">
          <img class="characteristic_img" src="../../assets/img/pp.png" />
          <div class="characteristic_txt">
            <div class="characteristic_name">这里是标题信息这里是标题信息</div>
            <div class="characteristic_desc">
              这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息
            </div>
          </div>
        </div>
      </div>
      <div class="paginaton">
        <el-pagination
          :background="false"
          :pager-count="9"
          layout="prev, pager, next"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :total="80"
        >
        </el-pagination>
      </div>
    </div>
    <div class="bottom"></div>
  </div>
</template>
<script>
export default {
  data() {
    return {};
  },
  methods: {
    handleSizeChange(val) {
      this.pageSize = val;
      this._getSearch();
      this.loading = true;
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this._getSearch();
      this.loading = true;
    },
  },
  created() {},
  components: {},
};
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
.product_list {
  background: #f9f9f9;
}
.bottom {
  height: 24px;
}
.product_main {
  width: 1200px;
  background: #ffffff;
  margin: 0 auto;
  padding: 1px 0;
}
.title {
  font-size: 20px;
  text-align: center;
  font-weight: bold;
  width: 1200px;
  margin: 40px auto 0;
  font-size: 20px;
  color: #0695d4;
}
.characteristic {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 1200px;
  padding: 0 24px;
  margin: 0 auto;
  .characteristic_info {
    width: 269x;
    margin-top: 25px;
    .characteristic_img {
      width: 269px;
      height: 194px;
    }
    .characteristic_txt {
      border: 1px solid #ededed;
      padding: 20px;
      width: 267px;
      position: relative;
      top: -5px;
    }
    .characteristic_name {
      font-size: 16px;
      color: #333333;
      font-weight: bold;
      line-height: 20px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    .characteristic_desc {
      font-size: 14px;
      color: #787878;
      line-height: 24px;
      margin-top: 8px;
    }
  }
}
</style>



